form.create-task {
  margin-top: 80px;
}
@media (max-width: 767.98px) {
  form.create-task {
    margin: 30px 20px 0 20px;
  }
}
@media (max-width: 767.98px) {
  form.create-task label {
    display: none;
  }
}
form h2.title {
  font-weight: 600;
  font-size: 16px;
  color: #364353;
}
@media (max-width: 991.98px) {
  form h2.title {
    font-weight: 500;
    font-size: 14px;
  }
}
@media (max-width: 767.98px) {
  form h2.title {
    margin-bottom: 16px;
    color: #809fb8;
  }
}
@media (max-width: 575.98px) {
  form h2.title {
    font-size: 12px;
    margin-bottom: 14px;
  }
}
form > .row:not(:last-child) {
  margin-bottom: 50px;
}
@media (max-width: 767.98px) {
  form > .row:not(:last-child) {
    margin-bottom: 40px;
  }
}
@media (max-width: 575.98px) {
  form > .row:not(:last-child) {
    margin-bottom: 30px;
  }
}

.form-material {
  position: relative;
}
.form-material.with-placeholder .form-material-input::placeholder,
.form-material.with-placeholder .form-select::placeholder {
  font-weight: 500;
  font-size: 14px;
  color: #809fb8;
  font-size: 0;
}
@media (max-width: 767.98px) {
  .form-material.with-placeholder .form-material-input::placeholder,
.form-material.with-placeholder .form-select::placeholder {
    font-size: 12px;
  }
}
.form-material .form-select {
  background: url(https://api.iconify.design/uil/angle-down.svg?color=%23809fb8) no-repeat right 0.75rem center/28px;
  font-weight: 500;
  font-size: 14px;
}
.form-material .form-select:invalid {
  color: #809fb8;
}
.form-material-wrapper {
  margin: 0 48px;
}
@media (max-width: 767.98px) {
  .form-material-wrapper {
    margin: 0 30px;
  }
}
@media (max-width: 575.98px) {
  .form-material-wrapper {
    margin: 0 20px;
  }
}
@media (max-width: 399.98px) {
  .form-material-wrapper {
    margin: 0 16px;
  }
}
.form-material-wrapper.form-add-profile button {
  font-size: 14px;
}
@media (max-width: 575.98px) {
  .form-material-wrapper.form-add-profile button {
    font-size: 12px;
  }
}
.form-material-wrapper.form-add-profile button:first-child {
  font-weight: 500;
  color: #75818e;
}
.form-material-button {
  display: flex;
  align-items: stretch;
  justify-content: center;
}
.form-material-button .form-material-input {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.form-material-button button.btn {
  flex: 0 0 auto;
  padding: 12px 22px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.form-material-input,
.form-material .form-select {
  border: 1px solid #dde6ee;
  border-radius: 6px;
  padding: 14px 21px;
  width: 100%;
  font-size: 14px;
}
@media (max-width: 767.98px) {
  .form-material-input,
.form-material .form-select {
    background-color: #fff;
    font-size: 12px;
  }
}
@media (max-width: 575.98px) {
  .form-material-input,
.form-material .form-select {
    padding: 12px 16px;
  }
}
.form-material-input:focus, .form-material-input:valid,
.form-material .form-select:focus,
.form-material .form-select:valid {
  outline: 0;
  border-color: #c7c0fe;
  box-shadow: none;
}
.form-material-input:focus + label, .form-material-input:valid + label,
.form-material .form-select:focus + label,
.form-material .form-select:valid + label {
  left: 12px;
  top: -10px;
  color: #6c5ce7;
  font-size: 12px;
  font-weight: 400;
}
.form-material-label {
  display: flex;
  align-items: center;
  position: absolute;
  top: 14px;
  left: 12px;
  font-weight: 500;
  font-size: 14px;
  color: #809fb8;
  transition: 0.25s ease all;
  padding: 0 8px;
  background-color: #fff;
}
@media (max-width: 575.98px) {
  .form-material-label {
    padding: 0 4px;
    font-size: 12px;
  }
}

.option-hidden {
  display: none;
}