@import "variables";
@import "breakpoints";
form {
	&.create-task {
		margin-top: 80px;

		@include breakpoint(md) {
			margin: 30px 20px 0 20px;
		}

		label {
			@include breakpoint(md) {
				display: none;
			}
		}
	}

	h2.title {
		font-weight: 600;
		font-size: 16px;
		color: $dark-10;

		@include breakpoint(lg) {
			font-weight: 500;
			font-size: 14px;
		}
		@include breakpoint(md) {
			margin-bottom: 16px;
			color: $gray-20;
		}
		@include breakpoint(sm) {
			font-size: 12px;
			margin-bottom: 14px;
		}
	}

	> {
		.row {
			&:not(:last-child) {
				margin-bottom: 50px;

				@include breakpoint(md) {
					margin-bottom: 40px;
				}
				@include breakpoint(sm) {
					margin-bottom: 30px;
				}
			}
		}
	}
}

.form-material {
	position: relative;

	&.with-placeholder {
		.form-material-input,
		.form-select {
			&::placeholder {
				font-weight: 500;
				font-size: 14px;
				color: $gray-20;
				font-size: 0;

				@include breakpoint(md) {
					font-size: 12px;
				}
			}
		}
	}

	.form-select {
		background: url(https://api.iconify.design/uil/angle-down.svg?color=%23809fb8) no-repeat right 0.75rem center / 28px;
		font-weight: 500;
		font-size: 14px;

		&:invalid {
			color: $gray-20;
		}
	}

	&-wrapper {
		margin: 0 48px;

		@include breakpoint(md) {
			margin: 0 30px;
		}
		@include breakpoint(sm) {
			margin: 0 20px;
		}
		@include breakpoint(xs) {
			margin: 0 16px;
		}

		&.form-add-profile {
			button {
				font-size: 14px;

				@include breakpoint(sm) {
					font-size: 12px;
				}

				&:first-child {
					font-weight: 500;
					color: $gray-1400;
				}
			}
		}
	}

	&-button {
		display: flex;
		align-items: stretch;
		justify-content: center;

		.form-material-input {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}

		button.btn {
			flex: 0 0 auto;
			padding: 12px 22px;
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
		}
	}

	&-input,
	.form-select {
		border: 1px solid $gray-500;
		border-radius: 6px;
		padding: 14px 21px;
		width: 100%;
		font-size: 14px;

		@include breakpoint(md) {
			background-color: $white;
			font-size: 12px;
		}
		@include breakpoint(sm) {
			padding: 12px 16px;
		}

		&:focus,
		&:valid {
			outline: 0;
			border-color: $gray-400;
			box-shadow: none;

			+ {
				label {
					left: 12px;
					top: -10px;
					color: $purple-10;
					font-size: 12px;
					font-weight: 400;
				}
			}
		}
	}

	&-label {
		display: flex;
		align-items: center;
		position: absolute;
		top: 14px;
		left: 12px;
		font-weight: 500;
		font-size: 14px;
		color: $gray-20;
		transition: 0.25s ease all;
		padding: 0 8px;
		background-color: $white;

		@include breakpoint(sm) {
			padding: 0 4px;
			font-size: 12px;
		}
	}
}
.option-hidden {
	display: none;
}
